import React from 'react';
import Header from '../header';
import Shop from '../shop';
import Cart from '../cart';
import { withDataPhone } from '../hoc';
import { Route } from 'react-router-dom';
import PhoneSingle from '../page/phone-single';

const App = ({ phoneservice }) => {
  return (
    <div>
      <Header />
      <Route
        exact
        path="/shop"
        render={({ history }) => {
          return (
            <div>
              <Shop data={phoneservice.getList()} />
            </div>
          );
        }}
      />

      <Route
        exact
        path="/shop/product/:id"
        render={({ match, history }) => {
          let { id } = match.params;
          return <PhoneSingle id={id} history={history} />;
        }}
      />
      <Route path="/shop/cart" component={Cart} />
    </div>
  );
};

export default withDataPhone()(App);
